<template>
  <div class="container">
    <template v-if="isLogin">
      <a href="">home</a>
    </template>
    <template v-else>
      <quiz></quiz>
    </template>
  </div>
</template>
<script>
import Quiz from './common/Quiz.vue'
export default {
  components:{Quiz},
  data() {
    return {
      msg: '欢迎登陆',
      isLogin: currentUser.isLogin
    }
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

</style>



