<template>
  <div id="quiz-wrap">
    <section>
      <h3>{{subject}}</h3>
      <form action="">
        <ul>
        <li class="quiz-item" v-for="(item,i) in quiz">
          <p class="bold">{{i+1}}.{{item.question}}</p>
          <p>
            <template v-for="(value,key,index) in item.options" class="mr10">
              <input type="radio" :name="item.code" :id="key+value" v-model="item.val" :value="value"><label class="mr20" :for="key+value">{{index | numToLetter}}{{key}}</label>
            </template>
          </p>
        </li>
       </ul>
       <p><button type="button" @click="isSubmited=!isSubmited">提交</button><span class="mr20"></span><button type="reset" @click="">重置</button></p>
      </form>
      
      
      <p :class="[isPositive? positiveClass : negativeClass]" v-if="isSubmited">得分:{{result}}</p>
    </section>
    <section>
      <h3>菲尔测试得分标准</h3>
      <table width="300" height="300" border="1">
        <tr>
          <td></td>
          <td>A</td>
          <td>B</td>
          <td>C</td>
          <td>D</td>
          <td>E</td>
          <td>F</td>
          <td>G</td>
        </tr>
        <tr>
          <td>1</td>
          <td>2</td>
          <td>4</td>
          <td>6</td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td>2</td>
          <td>6</td>
          <td>4</td>
          <td>7</td>
          <td>2</td>
          <td>1</td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td>3</td>
          <td>4</td>
          <td>2</td>
          <td>5</td>
          <td>7</td>
          <td>6</td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td>4</td>
          <td>4</td>
          <td>6</td>
          <td>1</td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td>5</td>
          <td>6</td>
          <td>4</td>
          <td>3</td>
          <td>5</td>
          <td></td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td>6</td>
          <td>6</td>
          <td>4</td>
          <td>2</td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td>7</td>
          <td>6</td>
          <td>2</td>
          <td>4</td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td>8</td>
          <td>6</td>
          <td>7</td>
          <td>5</td>
          <td>4</td>
          <td>3</td>
          <td>2</td>
          <td>1</td>
        </tr>
        <tr>
          <td>9</td>
          <td>7</td>
          <td>6</td>
          <td>4</td>
          <td>2</td>
          <td>1</td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td>10</td>
          <td>4</td>
          <td>2</td>
          <td>3</td>
          <td>5</td>
          <td>6</td>
          <td>1</td>
          <td></td>
        </tr>
      </table>
    </section>
    <section>
      <h3>菲尔博士的分析</h3>
      <p>将上述得分结果相加后，再参照后面分析</p>
    </section>
  </div>
</template>
<script>
export default {
  data() {
    return {
      positiveClass:'positive',
      negativeClass:'negative',
      isSubmited: false,
      subject: '菲尔性格测试题',
      quiz: [
        { question: '你何时感觉最好', code: 'q1001', val: 0, options: { '早晨': 2, '下午及傍晚': 4, '夜里': 6 } },
        { question: '你走路时是', code: 'q1002', val: 0, options: { '大步快走': 6, '小步快走': 4, '不快,仰着头面对着世界': 7, '不快，低着头': 2, '很慢': 1 } },
        { question: '和他人说话时，你', code: 'q1003', val: 0,options: { '双手交叠站着': 4, '双手紧握着': 2, '一只手或两只手放在臀部': 5, '碰着或推着与你说话的人': 7, '玩着你的耳朵，摸着你的下巴或用手整理头发': 6 } }
      ],

    }
  },
  computed:{
    result(){
      let [result] = [0];
      this.quiz.forEach((item,index,arr)=>{
         result += item.val;
      });
      return result;
    },
    isPositive(){
      if(this.result>=10){
        return true;
      }
    }
  },

  methods:{
    reset($event){
      $event.target.form.submit();
    }
  },
  filters: {
    numToLetter(n) {
      const map = ['A', 'B', 'C', 'D', 'E', 'F', 'G'];
      return map[n];
    }
  }
}

</script>
<style>

.positive{color:red;}
.negative{color:green;}
.mr20 {
  margin-right: 20px;
}


table {
  border-collapse: collapse;
}

#quiz-wrap {
  font: 'simsum'12px;

}

#quiz-wrap .quiz-item {
  margin-top: 5px;
  text-align: left;
}

.bold {
  font-weight: bold;
}

</style>



