export default class User {
  constructor() {
    this.username = ''
    this.password = ''
    this.phoneNumber = ''
    this.isLogon = false
  }

  logon({ userInfo }) {
    this.isLogon = true
    this.username = userInfo.username
    this.password = userInfo.password
    this.phoneNumer = userInfo.phoneNumer
  }

  logout() {
    this.isLogon = false
  }
}



